<script>
// @ is an alias to /src
//import HomeComp from '../components/HomeComp.vue'

export default {
  name: 'Home',
  data() {
    return {
      isShowSection_2: false,
      isShowSection_3: false,
      isShowSection_5: false,
      isShowSection_6: false

    }},
  methods: {
    showSection_2: function () {
      let elem = document.getElementById('section_2');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if(y < windowInnerHeight/3) {
        this.isShowSection_2 = true
      }
    },
    showSection_3: function () {
      let elem = document.getElementById('section_3');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if (y < windowInnerHeight / 3) {
        this.isShowSection_3 = true
      }
    },
    showSection_5: function () {
      let elem = document.getElementById('section_5');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if (y < windowInnerHeight / 2 && y > -windowInnerHeight) {
        this.isShowSection_5 = true;
        //elem.style.backgroundImage = 'url("../assets/images/venice_1920_blur.png")';
        elem.style.backgroundColor = "#fff";
      } else {
        this.isShowSection_5 = false;
        //elem.style.backgroundImage = 'url("../assets/images/venice_1920.jpg")';
        elem.style.backgroundColor = "#999";
      }
    },
    showSection_6: function () {
      let elem = document.getElementById('section_6');
      let position = elem.getBoundingClientRect();
      let y = position.top;
      let windowInnerHeight = document.documentElement.clientHeight;
      if (y < windowInnerHeight / 3 && y > -windowInnerHeight / 3) {
        this.isShowSection_6 = true;
        //elem.style.display = 'block';
        //elem.style.backgroundColor = "#fff";
      } else {
        this.isShowSection_6 = false;
        //elem.style.display = 'none';
        //elem.style.backgroundImage = 'url("../assets/images/venice_1920.jpg")';
        //elem.style.backgroundColor = "#999";
      }
    }
  },
  mounted: function(){
    window.addEventListener("scroll", this.showSection_2);
    window.addEventListener("scroll", this.showSection_3);
    window.addEventListener("scroll", this.showSection_5);
    window.addEventListener("scroll", this.showSection_6);
  },
  beforeRouteLeave: function(){
    window.removeEventListener("scroll", this.showSection_2);
    window.removeEventListener("scroll", this.showSection_3);
    window.removeEventListener("scroll", this.showSection_5);

  }
}

</script>

